    /* NAVBAR */
    @font-face {
        font-family: 'gfitfont';
        font-style: normal;
        font-weight: normal;
        src: local('gfitfont'), url(/gfitwefit/static/media/gfitfont.a4286c09.ttf), url(/gfitwefit/static/media/gfitfont.5d26fd49.woff) format('truetype');
