    .container {
        max-height: 50px !important;
    }

    .masterRow {
        height: 50px !important;
        max-width: 100% !important;
    }

    .navbar {
        position: fixed;
        margin-bottom: 0;
        background-color: white;
        border-top: solid 2px #B1864F;
        border-bottom: solid 3px #B1864F;
    }
    
    .navbar-brand {
        position: fixed !important;
        left: 15px !important;
        font-family: 'gfitfont';
        font-style: normal;
        font-weight: 300;
        font-size: 25px;
        color: #B1864F !important;
    }
    .navbar-brand:hover {
        background-color: transparent !important;
        color: white !important;
        cursor: pointer !important;
      }
    .navbar-toggler {
        position: absolute;
        right: 0;
        background-color: #B1864F !important;
    }
    .navbar-toggler-icon {
        color: #B1864F !important;
    }
    .nav-item {
        padding-bottom: 10px;
    }
    .nav-item:hover {
        text-shadow: 2px 2px #B1864F;
        background-color: transparent !important;
        color: black !important;
        cursor: pointer !important;
    }
    .navItems {
        position: absolute;
        z-index: 1;
        font-family: 'gfitfont';
        font-style: normal;
        font-weight: 300;
        background-color: black;
        padding: 15px;
        color: #B1864F !important;
        position: absolute;
        top: 45px;
        right: 1%;
        border-radius: 3%;
    }
    .nav-link {
        text-align: center !important;
        color: #B1864F !important;
        min-width: 100px !important;
    }
    .nav-link:hover {
        text-shadow: 2px 2px #B1864F;
        background-color: transparent !important;
        color: black !important;
    }
    .pages {
        position: absolute;
        right: 3%;
    }
    .iconBar {
        color: #B1864F !important;
        background-color: transparent !important;
    }
    
    /* a:hover {
        background-color: transparent !important;
        color: white !important;
    } */
    .navbar-toggler .iconBar {
        width: 22px;
        -webkit-transition: all 0.2s;
        transition: all 0.2s;
    }
    .navbar-toggler-icon {
        color: white !important;
    }
    .navbar-toggler .iconBar:nth-of-type(1) {
        -webkit-transform: rotate(45deg);
        transform: rotate(45deg);
        -webkit-transform-origin: 10% 10%;
        transform-origin: 10% 10%;
    }
    
    .navbar-toggler .iconBar:nth-of-type(2) {
        opacity: 0;
        filter: alpha(opacity=0);
    }
    
    .navbar-toggler .iconBar:nth-of-type(3) {
        -webkit-transform: rotate(-45deg);
        transform: rotate(-45deg);
        -webkit-transform-origin: 10% 90%;
        transform-origin: 10% 90%;
    }
    .navbar-toggler.collapsed .iconBar:nth-of-type(1) {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }
    
    .navbar-toggler.collapsed .iconBar:nth-of-type(2) {
        opacity: 1;
        filter: alpha(opacity=100);
    }
    
    .navbar-toggler.collapsed .iconBar:nth-of-type(3) {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }

